<script>
export default {
  name: "JoinUs",
  data() {
    return {
      form: {
        email: "",
        goodAt: "",
      },
      submitLoading: false,
      dialogActive: false,
    };
  },
  methods: {
    testEmail(email) {
      let re = /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
      if (re.test(email)) {
        return true;
      } else {
        return false;
      }
    },
    submit() {
      this.submitLoading = true;

      setTimeout(() => {
        this.submitLoading = false;
        this.dialogActive = true;
      }, 3000);
    },
  },
};
</script>

